main {
  width: 1200px;
  margin: 24px auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.status-bar, .log, .map {
  height: 580px;
  color: white;
}

.status-bar, .log {
  width: 20%;
  padding: 8px 16px;
}
.status-bar p, .log p {
  text-align: left;
}

.status-bar, .map {
  overflow: hidden;
}

.status-bar {
  background-color: #5f1a00;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}
.status-bar h2 {
  -ms-flex-item-align: center;
      align-self: center;
}

.map {
  width: 60%;
  background-color: black;
  font-size: 10px;
}

.log {
  background-color: #192c00;
  overflow: auto;
}
.log::-webkit-scrollbar {
  width: 10px;
}
.log::-webkit-scrollbar-thumb {
  background-color: #35530c;
  border-radius: 5px;
}
.log::-webkit-scrollbar-track {
  background-color: #192c00;
}
.log::-webkit-scrollbar-button {
  display: none;
}



